import { WebChart } from '@arcgis/charts-spec';
import { getItemData } from '@esri/arcgis-rest-portal';
import { Component, Host, Prop, Watch, h } from '@stencil/core';

/**
 * Displays an array of charts 
 * Initially given a webmap id
 */
@Component({
  tag: 'cedar-dashboard',
  styleUrl: 'cedar-dashboard.css',
  shadow: true,
})
export class CedarDashboard {

  @Prop() webmapId:string = null;

  @Prop({mutable:true, reflect: true}) charts: Array<WebChart> = [];

  componentWillLoad() {
    this.loadWebmap(this.webmapId)
  }

  @Watch('webmapId')
  async loadWebmap(newWebmapId:string) {
    const webmapData = await getItemData(newWebmapId);

    this.charts = webmapData.operationalLayers
                    .map((layer) => {
                      return layer.charts || []
                    })
                    .flat();

    console.debug("cedar-dashboard: loadWebmap", this.charts)
  }
  render() {
    return (
      <Host>
        <slot></slot>
        {this.charts.map((chart) => {

          return (
            <cedar-chart
              config={chart}
            ></cedar-chart>
          )
        })}
      </Host>
    );
  }

}
